
<template>
  <div id="page-content" class="header-static">
    <div class="container" @mouseup="mouseup" @mousemove="mousemove">
      <div
        class="drag-block block1"
        @mousedown="mousedown"
        :style="'top:'+B1Y+'px;'+'left:'+B1X+'px'"
      >mousedown/ up/ move 实现的拖拽</div>
      <div class="slide-bar"></div>
    </div>
  </div>
</template>
 
<script>
export default {
  data: function () {
    return {
      mousedownX: 0,
      mousedownY: 0,
      //block1 position
      draggingB1: false,
      B1X: 69,
      B1Y: 35,
    }
  },
  watch: {},
  computed: {},
  methods: {
    //mouse事件实现拖拽
    mousedown: function (event) {
      this.draggingB1 = true
      this.mousedownX = event.clientX
      this.mousedownY = event.clientY
    },
    mousemove: function (event) {
      if (this.draggingB1) {
        //鼠标位置
        this.B1X = this.B1X + event.clientX - this.mousedownX
        this.B1Y = this.B1Y + event.clientY - this.mousedownY
        console.log('dragging' + this.B1X + ',' + this.B1Y)
        this.mousedownX = event.clientX
        this.mousedownY = event.clientY
      }
    },
    mouseup: function (event) {
      console.log('drag over' + this.B1X + ',' + this.B1Y)
      this.draggingB1 = false
    },
  },
}
</script>
<style scoped>
.drag-block {
  width: 200px;
  height: 60px;
  background-color: black;
  color: white;
  cursor: move;
  user-select: none;
  position: relative;
}
.block1 {
  left: 69px;
  top: 35px;
}
</style>

